@font-face {
    font-family: 'Ashley';
    src: url("../fonts/AshleyCapitaliSofia.otf") format("truetype");
    font-style: normal;
    font-weight: normal; }


*, ::after, ::befor {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }


html {
    font-size: 62.5%; }

body {
    font-family: 'Roboto';
    background: #41442C;
    color: #fff;
    padding: 40px 92px;
    margin: 0 auto; }
a {
    text-decoration: none;
    color: #F2F1EE; }

.container {
    max-width: 1366px; }

.header {
    display: flex;
    justify-content: space-between;
    margin:  0 0 4rem 9.3rem; }
.logo {
    display: flex;
    align-items: center;
    width: 18rem;
    height: 5rem; }
.menu__list {
    display: flex;
    gap: 2rem;
    font-size: 1.6rem; }
.menu__list-item {
    list-style-type: none; }

.menu__list-link {}


.menu__btn {
    display: none; }

.lang {
    display: flex;
    align-items: center;
    font-size: 1.6rem; }
.reg {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #2F2C22;
    background: #EBD7B2;
    width: 18rem;
    font-size: 1.6rem;
    height: 4rem; }
.wrapper {
    display: flex;
    height: 62rem;
    box-sizing: border-box;
    border-bottom: 1px solid #76786E; }



.wrapper-left {
    width: 50%;
    box-sizing: border-box;
    padding-left: 9.3rem;
    position: relative; }



.wrapper-right {
    width: 50%; }



.main-title {
    font-family: "Ashley";
    font-size: 6rem;
    padding: 5.3rem 2rem 9.2px 0; }

.main-p {
    font-size: 1.6rem; }

.wrapper__left-p {
    padding: 0 0 2rem 0; }

.wrapper__left-img {
    border-radius: 0 154px 0 0;
    position: absolute;
    top: 43.6rem;
    left: 0; }

.right-img1 {
    border-radius: 154px  0 154px 0;
    margin-right: 1rem; }
.right-img2 {
    border-radius: 0 154px  0 154px; }

.about {

    height: 104rem; }

.about-top {
    display: flex;
    height: 30%; }
.about__top-left {
    width: 50%;
    height: 100%;
    border-bottom: 1px solid #76786E; }

.about__top-right {
    width: 50%;
    height: 100%;
    border-bottom: 1px solid #76786E;
    border-left: 1px solid #76786E; }


.title-h2 {
    box-sizing: border-box;
    font-family: "Ashley";
    font-size: 4.8rem;
    text-align: right;
    height: 100%;
    margin: 0;
    padding: 17rem 2rem 9rem 0; }
.about-wrapper__middlel {
    display: flex;
    height: 55%;
    height: 44.2rem;
    border-bottom: 1px solid #76786E; }
.about__middlel-left {
    width: 50%;
    height: 100%;
    border-right: 1px solid #76786E; }
.about__middlel-right {
    display: flex;
    width: 50%;
    height: 100%; }


.img4 {
    margin: 3rem 4rem 6rem 38rem; }

.about-p {
    margin: 18rem 20rem 0 0;
    height: 10rem; }

.about__middlel-right1 {
    height: 100%;
    width: 50%;
    border-right: 1px solid #76786E; }

.about-p2 {
    margin: 8rem 4rem  4rem 0rem; }

.about-p3 {
    margin: 0rem 9rem  0rem 0rem; }
.about-p4 {
    margin: 26rem 0rem  0rem 1rem;
    width: 80%; }

.about__bottom {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    font-size: 1.6rem; }
